import React, {useEffect, useState} from "react";
import emailjs from "@emailjs/browser";
import Toastify from 'toastify-js'
import './ContactForm.css';

function ContactForm() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  useEffect(() => emailjs.init("IkmB783CG82GnK7OL"), []);
  async function handleSubmit(event) {
    event.preventDefault();
    console.log(`Submitting ${name}, ${email}, ${message}`);
    //           
    if(!name || !email || !message) {
      Toastify({
        text: "      ",
        style: {
          background: "linear-gradient(to right, #FF0000, #FF6347)",
          color: "white",
        },
      }).showToast();
      return;
    }
    const serviceId = "service_j2hgq0j";
    const templateId = "template_7ucdiho";
    try {

      await emailjs.send(serviceId, templateId, {
        name: name,
        recipient: email,
        message: message,
        email: email
      });
      Toastify({
        text: " ,        ",
        duration: 3000,
        newWindow: true,
        close: true,
        gravity: "top", // `top` or `bottom`
        position: "left", // `left`, `center` or `right`
        stopOnFocus: true, // Prevents dismissing of toast on hover
        style: {
          background: "linear-gradient(to right, #00b09b, #96c93d)",
        },
      }).showToast();
    } catch (error) {
      console.log(error);
    }
  }

  return (
    <div className="contact-form" id={'contact-form'}>
      <div className="contact-form__info">
        <h2 className="contact-form__title">  </h2>
        <p className="contact-form__description">    -   , ,   :</p>
      </div>
      <form onSubmit={handleSubmit}>
        <label htmlFor="name" className="contact-form__label"></label>
        <input id="name" type="text" value={name} onChange={(e) => setName(e.target.value)} className="contact-form__input" />

        <label htmlFor="email" className="contact-form__label">Email</label>
        <input id="email" type="email" value={email} onChange={(e) => setEmail(e.target.value)} className="contact-form__input" />

        <label htmlFor="message" className="contact-form__label"></label>
        <textarea id="message" rows="5" value={message} onChange={(e) => setMessage(e.target.value)} className="contact-form__textarea" />

        <button type="submit" className="contact-form__button"></button>
      </form>
    </div>
  );
}

export default ContactForm;